import {generateColors, invertColor} from "@/utils";
import {BASE_SCORE_POINTS, MAX_POINTS_PER_ROUND, TOTAL_SECONDS} from "@/utils/constants";
import {GameActions, GameMutations, GameState, StoreContext} from "@/interfaces/store";
import {MoreOrLess, MovementDirection, PieData} from "@/types/movement";
import {ScoreAndTimeOptions} from "@/interfaces";
import {ActionTree, MutationTree} from "vuex";

function getScoreMultiplier(pieData: PieData | null): number {
    if (!pieData) return 1;

    const [a, b] = Object.values(pieData);

    const diff = Math.abs(a - b);

    return 1 / diff;
}

const state: GameState = {
    score: 0,
    secondsLeft: TOTAL_SECONDS,
    totalSecondsPlayed: 0,
    colors: [],
    direction: 'vertical',
    scoreColor: '',
    conditionalColor: '',
    moreOrLess: '',
    pieData: {}
}

const mutations: MutationTree<GameState> & GameMutations = {
    generatePieDataMutation(state: GameState) {
        const data: { [p: string]: number } = {};

        state.colors.map((item: string) => {
            data[item] = Math.random();
        });

        state.pieData = data;
    },
    selectConditionMutation(state: GameState, value?: MoreOrLess) {
        if (value) {
            state.moreOrLess = value;

            return;
        }

        state.moreOrLess = Math.random() > 0.5 ? 'more' : 'less';
    },
    selectDirectionMutation(state: GameState) {
        state.direction = Math.random() > 0.5 ? 'vertical' : 'horizontal';
    },
    generateColorsMutation(state: GameState) {
        state.colors = generateColors();
        state.scoreColor = invertColor(state.colors[state.direction === 'horizontal' ? 1 : 0], true);
        state.conditionalColor = state.direction === 'vertical' ? invertColor(state.colors[0], true) : '#000000';
    },
    setScoreMutation(state: GameState, value: number) {
        state.score = value;
    },
    setSecondsLeftMutation(state: GameState, value: number) {
        state.secondsLeft = value;
    },
    setTotalSecondsPlayedMutation(state: GameState, value: number) {
        state.totalSecondsPlayed = value;
    },
    changeScoreAndTimeMutation(state: GameState, options: ScoreAndTimeOptions) {
        const scoreMultiplier = getScoreMultiplier(state.pieData);
        const {isCorrectChoice} = options;
        const sign = isCorrectChoice ? 1 : -1;
        const secondsAdjustment = sign ? 1.3 : 1.3 * scoreMultiplier;
        const secondsAdjusted = state.secondsLeft + sign * secondsAdjustment;

        state.score = Math.floor(state.score + sign * Math.min(BASE_SCORE_POINTS * scoreMultiplier, MAX_POINTS_PER_ROUND));
        state.secondsLeft = Math.min(TOTAL_SECONDS, secondsAdjusted);
    }
}

const actions: ActionTree<GameState, void> & GameActions = {
    async checkCorrectAnswerAction(context: StoreContext<GameState, GameMutations>, move: MovementDirection) {
        const { state, commit } = context;

        if (!state.pieData) return;

        const comparison = state.moreOrLess === 'more' ? Math.max : Math.min;

        let correctResult: { color: string, value: number };

        Object.entries(state.pieData).forEach(([color, value]) => {
            if (!correctResult || comparison(correctResult.value, value) === value) {
                correctResult = {color, value};
            }
        });

        const colorMatchIndex = state.colors.findIndex((color) => color === correctResult.color);
        const possibleMoves: MovementDirection[] = state.direction === 'vertical' ? ['up', 'down'] : ['left', 'right'];
        const correctMove = possibleMoves[colorMatchIndex];

        commit('changeScoreAndTimeMutation', {isCorrectChoice: move === correctMove});
    },
    async prepareRoundAction(context: StoreContext<GameState, GameMutations>) {
        const { commit } = context;

        commit('selectDirectionMutation');
        commit('selectConditionMutation');
        commit('generateColorsMutation');
        commit('generatePieDataMutation');
    }
}

export default {
    namespaced: true,
    state,
    mutations,
    actions
}
