



















import {Component, Vue} from "vue-property-decorator";
import {mapState} from "vuex";
import {KeysToMap} from "@/types/movement";
import {GeneralState} from "@/interfaces/store";

const generalStateKeysToMap: KeysToMap<GeneralState> = [
    'isTouchDevice'
];

@Component({
  computed: {
    ...mapState('general', generalStateKeysToMap)
  }
})
export default class Help extends Vue {
  private isTouchDevice!: boolean;
  private isHelpShown = false;

  private toggleVisibility() {
    this.isHelpShown = !this.isHelpShown;

    this.$emit('show-toggle', this.isHelpShown);
  }

  get movementType(): string {
    return this.isTouchDevice ? 'swipe' : 'press arrow key';
  }
}
