








import {Component, Vue} from "vue-property-decorator";
import {mapState} from "vuex";
import {KeysToMap} from "@/types/movement";
import {GameState} from "@/interfaces/store";

const gameStateKeysToMap: KeysToMap<GameState> = [
  'score',
  'totalSecondsPlayed'
];

@Component({
  computed: {
    ...mapState('game', gameStateKeysToMap)
  }
})
export default class EndScreen extends Vue {
  private score!: number;
  private totalSecondsPlayed!: number;
}
