











import {Component, Vue} from "vue-property-decorator";
import {mapState} from "vuex";
import {Direction, KeysToMap} from "@/types/movement";
import {GameState} from "@/interfaces/store";

const gameStateKeysToMap: KeysToMap<GameState> = [
  'colors',
  'direction'
];

@Component({
  computed: {
    ...mapState('game', gameStateKeysToMap)
  }
})
export default class Sides extends Vue {
  private colors!: string[];
  private direction!: Direction;
}
