














import {Component, Vue} from "vue-property-decorator";
import TimeBar from './TimeBar.vue';
import {mapState} from "vuex";
import {KeysToMap} from "@/types/movement";
import {GameState} from "@/interfaces/store";
import {TOTAL_SECONDS} from "@/utils/constants";

const gameStateKeysToMap: KeysToMap<GameState> = [
    'moreOrLess',
    'scoreColor',
    'conditionalColor',
    'score',
    'secondsLeft'
];

@Component({
  components: {
    TimeBar
  },
  computed: {
    ...mapState('game', gameStateKeysToMap)
  }
})
export default class InGameDashboard extends Vue {
  private secondsLeft!: number;
  private score!: number;
  private scoreColor!: string;
  private moreOrLess!: string;
  private conditionalColor!: string;

  private step = window.innerWidth / TOTAL_SECONDS;

  get barWidth(): number {
    return Math.min(window.innerWidth, this.step * this.secondsLeft);
  }
}
