










import {Component, Vue} from "vue-property-decorator";
import {MovementDirection, KeysToMap} from "@/types/movement";
import {mapState} from "vuex";
import {GeneralState} from "@/interfaces/store";

const generalStateKeysToMap: KeysToMap<GeneralState> = [
  'isTouchDevice'
];

@Component({
  computed: {
    ...mapState('general', generalStateKeysToMap)
  }
})
export default class SwipeableArea extends Vue {
  public beginTouch: Touch | null = null;
  public endTouch: Touch | null = null;
  public diffX = 0;
  public diffY = 0;

  private isTouchDevice!: boolean;

  public onTouchStart(event: TouchEvent): void {
    this.diffX = 0;
    this.diffY = 0;
    event.preventDefault();

    this.beginTouch = event.touches.item(0);
  }

  public onTouchEnd(event: TouchEvent): void {
    event.preventDefault();

    this.endTouch = event.changedTouches.item(0);

    this.calculateMove();
  }

  private calculateMove(): void {
    // eslint-disable-next-line @typescript-eslint/no-non-null-assertion
    const {pageX: beginX, pageY: beginY} = this.beginTouch!;
    // eslint-disable-next-line @typescript-eslint/no-non-null-assertion
    const {pageX: endX, pageY: endY} = this.endTouch!;

    this.diffX = endX - beginX;
    this.diffY = endY - beginY;

    const absoluteDiffX = Math.abs(this.diffX);
    const absoluteDiffY = Math.abs(this.diffY);

    if (!absoluteDiffY && !absoluteDiffX) return;

    let result: MovementDirection;

    if (absoluteDiffX > absoluteDiffY) {
      // return;
      result = this.diffX > 0 ? 'right' : 'left';
    } else {
      result = this.diffY > 0 ? 'down' : 'up';
    }

    this.$emit('movement', result);
  }

  private handleKeys(e: KeyboardEvent) {
    if (this.isTouchDevice) return;

    let result: MovementDirection | null = null;

    const lowerCasedCode = e.code.toLowerCase();

    if (lowerCasedCode.includes('up')) result = 'up';
    if (lowerCasedCode.includes('down')) result = 'down';
    if (lowerCasedCode.includes('left')) result = 'left';
    if (lowerCasedCode.includes('right')) result = 'right';

    if (!result) return;

    this.$emit('movement', result);
  }

  mounted(): void {
    window.addEventListener('keydown', this.handleKeys);
  }

  beforeDestroy(): void {
    window.removeEventListener('keydown', this.handleKeys);
  }
}
