




























import {Component, Vue, Watch} from 'vue-property-decorator';
import SwipeableArea from "@/components/SwipeableArea.vue";
import {KeysToMap, MoreOrLess, MovementDirection, NumericChanger} from "@/types/movement";
import Pie from "@/components/Pie.vue";
import Help from "@/components/Help.vue";
import InGameDashboard from "@/components/InGameDashboard.vue";
import StartScreen from "@/components/StartScreen.vue";
import EndScreen from "@/components/EndScreen.vue";
import {TOTAL_SECONDS} from "@/utils/constants";
import Sides from "@/components/Sides.vue";
import {mapActions, mapMutations, mapState} from "vuex";
import {GameActions, GameMutations, GameState} from "@/interfaces/store";

const gameStateKeysToMap: KeysToMap<GameState> = [
  'secondsLeft',
  'totalSecondsPlayed'
];

const gameMutationKeysToMap: KeysToMap<GameMutations> = [
  'selectConditionMutation',
  'setSecondsLeftMutation',
  'setScoreMutation',
  'setTotalSecondsPlayedMutation'
];

const gameActionKeysToMap: KeysToMap<GameActions> = [
    'checkCorrectAnswerAction',
    'prepareRoundAction'
];

@Component({
  components: {
    Sides,
    EndScreen,
    StartScreen,
    Help,
    Pie,
    SwipeableArea,
    InGameDashboard
  },
  methods: {
    ...mapMutations('game', gameMutationKeysToMap),
    ...mapActions('game', gameActionKeysToMap),
  },
  computed: {
    ...mapState('game', gameStateKeysToMap),
  }
})
export default class Home extends Vue {
  private timeIntervalId!: number;
  private isMoving = false;
  private isGameOver = false;
  private isStartScreenShown = true;

  private secondsLeft!: number;
  private totalSecondsPlayed!: number;
  private selectConditionMutation!: (value?: MoreOrLess) => void;
  private setSecondsLeftMutation!: NumericChanger;
  private setScoreMutation!: NumericChanger;
  private setTotalSecondsPlayedMutation!: NumericChanger;
  private checkCorrectAnswerAction!: (move: MovementDirection) => void;
  private prepareRoundAction!: () => void;

  async mounted(): Promise<void> {
    await this.prepareRoundAction();
    this.selectConditionMutation('');
  }

  async resetGame(): Promise<void> {
    this.selectConditionMutation('');
    this.isStartScreenShown = false;
    this.isGameOver = false
    this.setSecondsLeftMutation(TOTAL_SECONDS);
    this.setScoreMutation(0);
    this.setTotalSecondsPlayedMutation(0);

    await this.prepareRoundAction();
    this.countSeconds();
  }

  countSeconds(): void {
    clearInterval(this.timeIntervalId);

    if (this.isStartScreenShown || this.isGameOver) return;

    this.timeIntervalId = setInterval(() => {
      this.setSecondsLeftMutation(this.secondsLeft - 1);
      this.setTotalSecondsPlayedMutation(this.totalSecondsPlayed + 1);

      if (!this.secondsLeft || this.secondsLeft < 0) {
        clearInterval(this.timeIntervalId);

        this.isGameOver = true;
        this.setSecondsLeftMutation(0);
      }
    }, 1000);
  }

  get isGameNotStarted(): boolean {
    return this.isStartScreenShown || this.isGameOver;
  }

  @Watch('isGameNotStarted') checker(val: boolean): void {
    if (val) this.selectConditionMutation('');
  }

  onHelpToggle(flag: boolean): void {
    if (flag) {
      clearInterval(this.timeIntervalId);
    } else {
      this.countSeconds();
    }
  }

  async showMovement(direction: MovementDirection): Promise<void> {
    if (this.isMoving || this.isGameNotStarted) return;

    this.isMoving = true;

    const pie = this.$refs.pie as Pie;

    await pie.move(direction);

    await this.checkCorrectAnswerAction(direction);

    await this.prepareRoundAction();

    this.isMoving = false;
  }
}
