









import {Component, Prop, Vue, Watch} from "vue-property-decorator";
import {v4 as uuid} from 'uuid';
import * as d3 from 'd3';
import {MovementDirection, PieData, KeysToMap} from "@/types/movement";
import {mapState} from "vuex";
import {GameState} from "@/interfaces/store";

const gameStateKeysToMap: KeysToMap<GameState> = [
  'pieData',
  'colors'
];

@Component({
  computed: {
    ...mapState('game', gameStateKeysToMap)
  }
})
export default class Pie extends Vue {
  public circleId = `circle_${uuid()}`;
  public movementClass = '';
  private movementStyle = {};
  private isHidden = true;
  private isMoving = false;
  private pieData!: PieData;
  private colors!: string[];

  @Prop({default: 50}) readonly size!: number;

  get wrapperStyle(): { [p: string]: string } {
    return {
      width: `${this.size}px`,
      height: `${this.size}px`
    }
  }

  getTranslate(direction: MovementDirection): string {
    const isPositive = ['down', 'right'].includes(direction);
    const axis = ['up', 'down'].includes(direction) ? 'Y' : 'X';
    const offset = (isPositive ? 1 : -1) * (axis === 'X' ? window.innerWidth : window.innerHeight) / 2;

    return `translate${axis}(${offset}px)`;
  }

  move(direction: MovementDirection): Promise<void> {
    this.isMoving = true;
    this.movementStyle = {
      transform: this.getTranslate(direction)
    }
    this.movementClass = direction;

    return new Promise((resolve) => {
      setTimeout(() => {
        this.isHidden = true;
        this.isMoving = false;
        this.movementClass = '';
        this.movementStyle = {};

        resolve();
      }, 310);
    });
  }

  drawPie(): void {
    this.isHidden = false;

    d3.select(`#${this.circleId} svg`).remove();

    const svg = d3
        .select(`#${this.circleId}`)
        .append('svg')
        .attr("viewBox", [-this.size / 2, -this.size / 2, this.size, this.size].join(','))
        .attr('width', this.size);

    const pie = d3.pie().value(d => d.valueOf());

    const formattedPieData = Object.entries(this.pieData).map(([key, value]) => {
      return {key, value, valueOf: () => value}
    })
    const preparedPieData = pie(formattedPieData);
    const arc = d3.arc();

    svg
        .selectAll('path')
        .data(preparedPieData)
        .enter()
        .append('path')
        .attr('d', (d) => {
          return arc({
            innerRadius: 0,
            outerRadius: this.size / 2,
            startAngle: d.startAngle,
            endAngle: d.endAngle
          });
        })
        .attr('fill', (d, i) => {
          const found = preparedPieData[i] as unknown as { data: { key: string } };

          return found.data.key;
        })
  }

  @Watch('pieData') newColors(): void {
    this.drawPie();
  }
}
